.meetup-events {
  padding: 40px 80px;
}
.meetup-events .meetup-welcome {
  color: #fff;
  margin-bottom: 40px;
}
.meetup-events .data-container {
  background-color: #fff;
  color: #777;
  border-radius: 4px;
  padding: 30px;
  text-align: left;
  line-height: 32px;
  width: 100%;
}
.meetup-events .data-container .date {
  width: 140px;
}
.meetup-events .data-container .type {
  width: 180px;
}
.meetup-events .data-container .details {
  width: 160px;
}
.meetup-events .data-container .event-info {
  width: 100%;
  margin-bottom: 20px;
}
.meetup-events .data-container .event-info thead {
  line-height: 40px;
  color: #00d680;
  font-size: 18px;
  font-weight: bold;
}
.meetup-events .data-container .event-info tbody a {
  color: #00d680;
  text-decoration: none;
}
.meetup-events .data-container .partition {
  border-color: #00d680;
}


