.login-container {
  color: #0ee;
  font-weight: bold;
  text-align: center;
  background-color: #fff;
  width: 300px;
  padding: 40px 40px;
}
.login-container .player-login {
  margin-bottom: 30px;
}
.login-container .auth-buttons {
  margin-bottom: 30px;
}
.login-container .auth-buttons .auth-button {
  display: block;
  margin-left: auto;
  margin-right: auto;
  text-align: center;
  width: 200px;
  padding: 4px;
  line-height: 28px;
  color: #777;
  background-color: #dfdfdf;
  text-decoration: none;
  margin-bottom: 10px;
  border-radius: 4px;
  position: relative;
}
.login-container .auth-buttons .auth-button-icon {
  height: 24px;
  position: absolute;
  left: 8px;
  top: 6px;
}
.login-container .privacy {
  font-size: 12px;
}


