@import "~normalize.css/normalize.css";
.pmg-button.button {
  display: inline-block;
  background: -webkit-linear-gradient(left, #00d680, #0ee);
  background: linear-gradient(to right, #00d680, #0ee);
  border: none;
  color: #fff;
  padding-left: 40px;
  padding-right: 40px;
  padding-top: 4px;
  padding-bottom: 4px;
  line-height: 25px;
  border-radius: 25px;
}
ul {
  padding: 0;
  margin: 0;
}
h1,
h2,
h3,
h4,
h5,
h6 {
  margin: 0;
}
a {
  text-decoration: none;
}
select {
  text-indent: 8px;
  -webkit-appearance: none;
     -moz-appearance: none;
          appearance: none;
  width: 80px;
  font-size: 17px;
  background: transparent;
  border: none;
  border-radius: 0;
  color: #fff;
  border-bottom: 3px solid #fff;
  cursor: pointer;
}
input {
  border: none;
  line-height: 22px;
}
.pull-left {
  float: left;
}
.pull-right {
  float: right;
}
.clearfix:before,
.clearfix:after {
  content: " ";
  display: table;
}
.clearfix:after {
  clear: both;
}
.close {
  cursor: pointer;
  position: fixed;
  right: 20px;
  top: 20px;
  display: inline-block;
  width: 50px;
  height: 50px;
  overflow: hidden;
}
.close:hover::before,
.close:hover::after {
  background-color: #325caf;
}
.close::before,
.close::after {
  content: '';
  position: absolute;
  height: 2px;
  width: 100%;
  top: 50%;
  left: 0;
  margin-top: -1px;
  background-color: #777;
}
.close::before {
  -webkit-transform: rotate(45deg);
          transform: rotate(45deg);
}
.close::after {
  -webkit-transform: rotate(-45deg);
          transform: rotate(-45deg);
}
html,
body {
  height: 100%;
  width: 100%;
}
body {
  background: -webkit-linear-gradient(top left, #0ee, #00d680);
  background: linear-gradient(to bottom right, #0ee, #00d680);
}
#app {
  height: 100%;
}
.left-sidebar {
  position: fixed;
  left: 0;
  top: 0;
}
.router-view {
  margin-left: 240px;
  padding-left: 40px;
  overflow: auto;
}
.fade-transition {
  -webkit-transition: opacity 0.2s ease;
  transition: opacity 0.2s ease;
}
.fate-enter,
.fade-leave {
  opacity: 0;
}


