.pokemon-viewer-details {
  height: 100%;
  width: 100%;
  position: fixed;
  left: 0;
  top: 0;
  background-color: rgba(200,200,200,0.8);
  z-index: 2;
}
.pokemon-viewer-details .pokedex-card {
  box-shadow: 0 0 10px rgba(0,0,0,0.14), 0 4px 8px rgba(0,0,0,0.28);
  margin-top: 200px;
  opacity: 1;
  display: block;
  margin-left: auto;
  margin-right: auto;
}


