.team-standing {
  display: block;
}
.team-standing .standing-summary {
  color: #fff;
  padding: 30px 0;
  font-size: 20px;
}
.team-container {
  margin-right: 80px;
  display: inline-block;
}
.team-container .team-card {
  width: 240px;
  margin-bottom: 30px;
}
.team-container .team-country-info {
  width: 240px;
  background-color: #fff;
  border-radius: 4px;
  margin-bottom: 30px;
}
.team-container .team-country-info table {
  width: 100%;
  padding: 10px;
}
.team-container .team-country-info table .country {
  text-align: left;
}
.team-container .team-country-info table .percent {
  text-align: right;
  width: 40px;
}
.team-container .team-country-info table thead th {
  font-size: 16px;
  color: #00d680;
  line-height: 40px;
  font-size: 18px;
  font-weight: bold;
}
.team-container .team-country-info table tbody td {
  font-size: 14px;
  color: #777;
}
.team-container .view-on-map-container {
  width: 240px;
}
.team-container .view-on-map-container .view-on-map {
  display: block;
  display: block;
  margin-left: auto;
  margin-right: auto;
  width: 200px;
  height: 40px;
  line-height: 40px;
  color: #fff;
  text-align: center;
  border: 2px #fff solid;
  border-radius: 20px;
}
.team-container .view-on-map-container .view-on-map.instinct {
  background-color: #ffd600;
}
.team-container .view-on-map-container .view-on-map.mystic {
  background-color: #006af6;
}
.team-container .view-on-map-container .view-on-map.valor {
  background-color: #f00;
}


