.profile {
  background-color: #fff;
  width: 200px;
  padding: 30px 40px 40px;
  border-radius: 4px;
  color: #777;
  text-align: center;
}
.profile .level,
.profile .xp-bar,
.profile .start-date {
  margin-bottom: 16px;
}
.profile .name {
  color: #0ee;
  margin-bottom: 16px;
  font-size: 32px;
}
.profile .level {
  font-size: 14px;
}
.profile .xp-details {
  margin-bottom: 16px;
}
.profile .journal {
  width: 160px;
  margin-bottom: 16px;
  line-height: 32px;
  border-radius: 16px;
  color: #fff;
  display: block;
  margin-left: auto;
  margin-right: auto;
  background: -webkit-linear-gradient(left, #00d680, #0ee);
  background: linear-gradient(to right, #00d680, #0ee);
}


