.team-card {
  width: 240px;
  background-color: #fff;
  border-radius: 4px;
  text-align: center;
  color: #777;
}
.team-card .team-image-container {
background-repeat
  background-position: center;
}
.team-card .team-name {
  padding-top: 20px;
}
.team-card .team-image {
  width: auto;
  height: 100px;
}
.team-card .team-name,
.team-card .team-image-container,
.team-card .team-gyms,
.team-card .team-trainers {
  line-height: 100%;
  margin-bottom: 20px;
}
.team-card .team-trainers {
  padding-bottom: 20px;
}
.team-card.instinct .team-name,
.team-card.instinct .trainer-count {
  color: #ffd600;
}
.team-card.mystic .team-name,
.team-card.mystic .trainer-count {
  color: #006af6;
}
.team-card.valor .team-name,
.team-card.valor .trainer-count {
  color: #f00;
}


