.pokedex-container {
  width: 720px;
  height: 420px;
  background-color: #fff;
  display: block;
  margin-left: auto;
  margin-right: auto;
}
.pokedex-container .pkm {
  display: block;
  display: block;
  margin-left: auto;
  margin-right: auto;
}
.pokedex-container .pokemon-avatar {
  width: 320px;
  margin-top: 80px;
}
.pokedex-container .pokemon-avatar img {
  display: block;
  display: block;
  margin-left: auto;
  margin-right: auto;
  width: 86%;
  height: auto;
  margin-bottom: -60px;
}
.pokedex-container .pokemon-avatar .oval-container {
  display: block;
  margin-left: auto;
  margin-right: auto;
  width: 200px;
  height: 80px;
  border-radius: 50%;
  background-color: rgba(255,255,255,0.7);
}
.pokedex-container .pokemon-viewer {
  height: 100%;
}
.pokedex-container .pokemon-card {
  width: 380px;
  height: 100%;
  overflow: hidden;
  display: block;
  margin-left: auto;
  margin-right: auto;
  box-shadow: -10px 0 10px rgba(0,0,0,0.1), 0 4px 8px rgba(0,0,0,0.2);
}
.pokedex-container .pokemon-card .pokemon-card-header {
  color: #fff;
  background: -webkit-linear-gradient(top, #df4a34, #ee7168);
  background: linear-gradient(to bottom, #df4a34, #ee7168);
  padding: 25px 40px;
}
.pokedex-container .pokemon-card .pokemon-card-header .pokemon-order .pokemon-ball-icon,
.pokedex-container .pokemon-card .pokemon-card-header .pokemon-order .pokemon-order-number {
  font-size: 16px;
  display: inline-block;
  line-height: 40px;
}
.pokedex-container .pokemon-card .pokemon-card-header .pokemon-name {
  font-size: 36px;
  font-weight: bold;
  line-height: 40px;
  text-transform: uppercase;
}
.pokedex-container .pokemon-card .pokemon-card-header .pokemon-type {
  display: inline-block;
  line-height: 40px;
  margin-right: 10px;
}
.pokedex-container .pokemon-card .pokemon-card-properties {
  color: #777;
  padding: 16px 0;
  background-color: #f8f8f8;
}
.pokedex-container .pokemon-card .pokemon-card-properties .pokemon-weight,
.pokedex-container .pokemon-card .pokemon-card-properties .pokemon-height,
.pokedex-container .pokemon-card .pokemon-card-properties .pokemon-weak-against {
  display: inline-block;
  line-height: 40px;
  width: 33%;
  text-align: center;
}
.pokedex-container .pokemon-card .pokemon-card-properties .pokemon-weight {
  border-right: 1px solid #ccc;
  border-left: 1px solid #ccc;
}
.pokedex-container .pokemon-card .pokemon-card-details {
  color: #777;
}
.pokedex-container .pokemon-card .pokemon-card-details .pkm-types-container {
  margin: 20px 40px;
}
.pokedex-container .pokemon-card .pokemon-card-details .pkm-types-container .pkm-type {
  display: inline-block;
  margin-right: 20px;
  line-height: 24px;
  width: 80px;
  border-radius: 12px;
  text-align: center;
}
.pokedex-container .pokemon-card .pokemon-card-details .pokemon-details {
  margin: 0 40px;
  line-height: 26px;
}


