.login-button {
  border-radius: 4px;
  border: #0ee 1px solid;
  width: 180px;
  height: 40px;
  line-height: 40px;
  text-align: center;
  color: #0ee;
  cursor: pointer;
}
.login-button:hover {
  background-color: #00d680;
  color: #fff;
}


