@import '~styles/pokemons.css';
.pokemon {
  cursor: pointer;
  width: 160px;
  background-color: #fff;
  border-radius: 4px;
  padding-bottom: 20px;
  color: #00d680;
  text-weight: bold;
}
.pokemon .pkm {
  display: block;
  margin-left: auto;
  margin-right: auto;
  width: 128px;
  height: 128px;
}
.pokemon .pkm-name {
  text-align: center;
  margin-bottom: 10px;
}
.pokemon .pkm-type-container {
  text-align: center;
}
.pokemon .pkm-type-container .pkm-type {
  display: inline-block;
  height: 20px;
  width: 64px;
  text-align: center;
  border-radius: 2px;
  margin: 0 6px;
}


