.login-view {
  width: 100%;
  height: 100%;
  position: fixed;
  top: 0;
  left: 0;
  background-color: rgba(200,200,200,0.8);
  background: -webkit-linear-gradient(left, #00d680, #0ee);
  background: linear-gradient(to right, #00d680, #0ee);
}
.login-view .pokemon-go-logo {
  width: 200px;
  display: block;
  margin-left: auto;
  margin-right: auto;
  margin-bottom: 30px;
  padding-top: 80px;
}
.login-view .login {
  display: block;
  margin-left: auto;
  margin-right: auto;
}
.login-view .close:before,
.login-view .close:after {
  background-color: #777;
}


