.pokedex {
  height: 100%;
  padding: 0 40px;
}
.pokedex .filter {
  font-size: 16px;
}
.pokedex .filter .choose-type,
.pokedex .filter .candy-range {
  display: inline-block;
  margin-right: 10px;
}
.pokedex .filter .type {
  display: inline-block;
  width: 80px;
  margin-right: 10px;
}
.pokedex .filter .gte,
.pokedex .filter .lte,
.pokedex .filter display inline-block {
  width: 60px;
  text-indent: 8px;
  color: #777;
}
.pokedex .filter .patition {
  padding: 0 4px;
}
.pokedex .pokedex-title {
  color: #fff;
  padding: 30px 0;
  font-size: 20px;
  width: 100%;
}
.pokedex .pokemon {
  display: inline-block;
  margin-bottom: 20px;
  margin-right: 20px;
}


