.progress-bar {
  position: relative;
  height: 8px;
}
.progress-bar .progress {
  position: absolute;
  top: 0;
  left: 0;
  background-color: #0ee;
  height: 8px;
  width: 50%;
}
.progress-bar .full {
  position: absolute;
  top: 2px;
  left: 0;
  background-color: #aaa;
  height: 4px;
  width: 100%;
}


